
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
  components: {
    Actionsheet,
    XButton
  },
  data () {
    return {
      face: '../../static/img/face.png',
      show: false,
      menus: {
        menu1: '',
        menu2: ''
      }
    }
  },
  created () {
    this.$store.state.vux.isLoading = false
  },
  methods: {
    click (e) {
      console.info('e', e)
    }
  }
}



